@import "var.scss";
@import "icon-vne.scss";
/*SWIPEBOX SLIDER*/
@import "slideshow-ver2.scss";
/*END SWIPEBOX SLIDER*/
/**BOX COMMENT**/
@import "comment.scss";
/**END BOX COMMENT**/
@import "scrollbar.scss";

/**ARTICLE BODY**/
.author_mail {
	text-align: right;
	font-size: px_to_rem(18px);
	color: #d6d6d6;
	margin-bottom: 15px;
	position: relative;

	p {
		font-size: px_to_rem(18px);
		line-height: $lh;

		.ic {
			/*display: inline-block;*/
			display: inline-block;
			vertical-align: middle;
			margin-top: 0px;
		}

		strong {
			color: #333;
		}
	}
}

.box_multipages {
	margin-left: -20px;

	.art_item {
		padding-left: 20px;
		margin-bottom: 20px;

		&:before {
			display: none;
		}

		.img {
			display: block;
			border: 1px solid #eee;
			margin-bottom: 10px;
		}

		.name {
			float: left;
			width: 100%;
			text-align: center;
			font: bold px_to_rem(18px) iCielDomaineText;
			line-height: $lh;
		}
	}

	.content {
		font-size: px_to_rem(18px);
		padding-left: 20px;
		font-family: arial;
		margin-bottom: 10px;

		h3 {
			font-weight: 400;
			font-style: italic;
			margin-bottom: 30px;
		}

		.control {
			color: #666;
			font-weight: bold;
			float: left;
			position: relative;

			&:before {
				content: "";
				background: #999;
				position: absolute;
				height: 2px;
				left: 20px;
				right: 0;
				bottom: 0;
			}

			&.next {
				float: right;

				.icon-ns {
					margin: 4px 0 0 5px;
					float: right;
				}

				&:before {
					right: 20px;
					left: 0;
				}
			}

			.icon-ns {
				color: #666;
				margin: 4px 5px 0 0;
				float: left;
			}
		}
	}
}

/**END ARTICLE BODY**/

/**Box xem thm**/
.xemthem_new_ver {
	padding-top: 15px;
	border-top: 1px solid #e2e2e3;

	.text_xemthem {
		float: left;
		font-size: px_to_rem(18px);
		margin-bottom: 10px;
	}
}

.list_news_xemthem li {
	margin-bottom: 10px;

	a {
		color: #004f8b;
	}
}

/**TIN LIEN QUAN**/
.title_box_samenews {
	&.title_box_border {
		padding-top: 20px;
		border-top: 2px solid #e7e7e7;
	}

	.title_samenews {
		font: 700 px_to_rem(24px) $fontmain;
		color: #333;
		line-height: $lh;
	}
}

.box_thesamenews {
	position: relative;

	.art_item {
		padding: 0;

		&:after {
			left: 0;
			width: 100%;
		}

		&.last {
			margin-bottom: 20px
		}

		.title_news {
			font: bold 1.125rem iCielDomaineText;
			line-height: 1.3;
		}
	}

	&.box_border {
		margin-bottom: 30px;

		&:before {
			content: "";
			background: #e7e7e7;
			position: absolute;
			left: 15px;
			right: 15px;
			height: 2px;
			bottom: 0;
		}
	}

	.load-more2 {
		padding: 0
	}
}

.list_art_text_noboder {
	padding-left: 12px;

	h3,
	h4 {
		margin-bottom: 10px;
		padding-bottom: 10px;
		padding-top: 0;
		border: none;
		font: 500 px_to_rem(18px) $fontmain;
		border-bottom: 1px solid #eaeaea;
		position: relative;
		line-height: 1.4;

		&:before {
			content: "";
			background: #999;
			width: 4px;
			height: 4px;
			position: absolute;
			left: -10px;
			top: 10px;
		}

		&:last-child {
			margin-bottom: 0;
			padding-bottom: 5px;
			border-bottom: 0;
		}
	}
}

/**BOX RECOMMEDATION**/
.box_recommendation {
	.title_box_samenews {
		&.title_box_border {
			padding-top: 10px;
			border-top: 1px solid #CCC;
		}

		.title_samenews {
			font: 700 px_to_rem(20px) arial;
		}
	}

	.xemtiep {
		&::before {
			display: none;
		}

		a {
			span {
				background: none;
				padding: 10px 20px 0 20px;
			}
		}
	}
}

/*BOX TOPIC*/
.list_news_topic li {
	margin-bottom: 10px;

	a {
		font: 700 px_to_rem(16px) arial;
		line-height: $lh;
	}

	span {
		font-size: px_to_rem(14px);
		font-weight: 400;
	}
}

.view_more_text {
	font: 400 px_to_rem(12px) arial;
	color: #666;
	margin: 10px 0 0 0;

	.ic-caret-2-right {
		background-position: -173px -6px;
	}
}


/**BOX DUNG BO LO, BOX SHOP VNE**/
.box_redcom_ads {
	.title_box_category {
		margin-bottom: 0;
	}

	.list_news_common .art_item {
		margin-bottom: 5px;
		border-bottom: none;

		&:last-child {
			margin-bottom: 0;
		}
	}

	.metadate {
		color: #959595;
		margin-top: 5px;
	}

	.title_news a {
		font: 400 px_to_rem(16px) arial;
		line-height: $lh;
	}
}

.box_shopads_vne {
	.metaprice {
		color: #d41212;
	}

	/*.title_box_category{border-top: none;}*/
	.title_box_category.title_box_category_style2 a,
	.title_box_category.title_box_category_style2 span {
		color: #9d9d9d;
		/*&::before{display: none;}*/
	}
}

/**BOX TAG**/
.block_tag {
	.block_scroll {
		overflow-x: scroll;
		-webkit-overflow-scrolling: touch;
		white-space: nowrap;
		-webkit-backface-visibility: hidden;
		backface-visibility: hidden;
	}

	position: relative;
	padding-left: 55px;

	.txt_tag {
		font: 500 px_to_rem(16px) $fontmain;
		color: #666;
		top: 0;
		left: 0;
		position: absolute;
		height: 36px;
		line-height: 36px;
		display: inline-block;
	}

	.list_tag_scroll {
		display: table-cell;
		vertical-align: middle;

		h3,
		h5 {
			display: table-cell;
			padding-right: 10px;

			&:last-child {
				padding-right: 0;
			}
		}
	}

	.tag_item {
		display: inline-block;
		margin-right: 4px;
		font: 400 15px/36px arial !important;
		color: #666;
		padding: 0 10px;
		background: #f1f1f1;
		border-radius: 4px;
	}
}

/**END BOX TAG**/

/*box_cothebanquantam*/
.box_cothebanquantam {
	padding-left: 10px;
	padding-right: 10px;

	.title_box {
		padding: 10px 0;
		border-top: 1px solid #e4e4e4;
		font: 700 px_to_rem(20px) arial;
		line-height: $lh;
		color: #000;
	}
}

.list_news_quantam {
	font-size: 0;
	margin-left: -10px;

	.list_news {
		width: 50%;
		display: inline-block;
		vertical-align: top;
		float: none;
		padding-left: 10px;

		&:nth-child(n + 5) {
			margin-top: 10px;
		}
	}

	.thumb_art {
		width: 100% !important;
		float: left;
		margin-bottom: 10px;

		img {
			width: 100%;
			float: left;
		}
	}

	.title_news {
		a {
			font: 400 16px/12px arial;
			color: #333
		}
	}
}

/*End box_cothebanquantam*/
.clear {
	clear: both;
}

.margin_15 {
	margin: 0 -10px 1em !important;
}

/*Detail Longform*/
.page_longfrom {
	.block_share_sticky {
		padding-top: 5px;
		padding-right: 15px;
	}

	#header_sticky {
		.count_comment_total {
			.count_comment {
				font: 400 14px/28px arial !important;
				margin-left: 10px;
			}
		}

		.item_social {
			opacity: 1;
			margin: 0;
			line-height: 30px !important;
			background: none;
			float: none;
		}
	}

	.item_social {
		background: none;

		.ic {
			&:before {
				display: none;
			}
		}
	}

	.count_comment_total {
		margin-left: 10px;

		.item_social {
			border: 0 !important;
			font-size: 16px !important;
			float: right !important;
		}

		.count_comment {
			padding: 0 10px !important;
			font: 400 14px/28px arial !important;
			color: #999 !important;
			border: 0;
			margin: 1px 10px 0 0;

			&:after {
				display: none;
			}

			&:before {
				right: -5px;
				top: 50%;
				margin-top: -5px;
				content: "";
				left: inherit;
				border-right: 0;
			}
		}
	}

	.ic {
		&:before {
			display: none;
		}
	}

	#breadcrum {
		.menu_bread {
			li {
				a {
					font-weight: 400;
				}

				&.active {
					a {
						font-weight: 700
					}
				}
			}
		}
	}
}

#nytmm {
	float: left;
	width: 100%
}

.nytmm-chapter-0 .nytmm-menu-section-0,
.nytmm-chapter-1 .nytmm-menu-section-1,
.nytmm-chapter-2 .nytmm-menu-section-2,
.nytmm-chapter-3 .nytmm-menu-section-3,
.nytmm-chapter-4 .nytmm-menu-section-4,
.nytmm-chapter-5 .nytmm-menu-section-5,
.nytmm-chapter-6 .nytmm-menu-section-6,
.nytmm-chapter-7 .nytmm-menu-section-7 {
	color: #333;
	font-weight: 700 !important;
}

.nytmm-chapter-0 .section_1,
.nytmm-chapter-1 .section_2,
.nytmm-chapter-2 .section_3,
.nytmm-chapter-3 .section_4,
.nytmm-chapter-4 .section_5,
.nytmm-chapter-5 .section_6,
.nytmm-chapter-6 .section_7 {
	opacity: 1 !important;
	display: block !important;
}

#nytmm.nytmm-chapter-1 #nytmm_prev_label_1,
#nytmm.nytmm-chapter-2 #nytmm_prev_label_2,
#nytmm.nytmm-chapter-3 #nytmm_prev_label_3,
#nytmm.nytmm-chapter-4 #nytmm_prev_label_4,
#nytmm.nytmm-chapter-5 #nytmm_prev_label_5,
#nytmm.nytmm-chapter-6 #nytmm_prev_label_6,
#nytmm.nytmm-chapter-0 #nytmm_next_label_1,
#nytmm.nytmm-chapter-1 #nytmm_next_label_2,
#nytmm.nytmm-chapter-2 #nytmm_next_label_3,
#nytmm.nytmm-chapter-3 #nytmm_next_label_4,
#nytmm.nytmm-chapter-4 #nytmm_next_label_5,
#nytmm.nytmm-chapter-5 #nytmm_next_label_6 {
	display: block;
}

.longform_wrapper {
	.page_section {
		display: none;
		float: left;
		width: 100%;
		padding-bottom: 70px;
	}

	blockquote {
		border-left: 5px solid #ccc;
		padding-left: 20px;
		margin: 0 0 20px;
		font-size: 24px;
		margin-bottom: 20px;
		font-weight: bold;
		line-height: 1.313;
	}

	.list_photo {
		margin-bottom: 0 !important;

		li.iterm_cate {
			float: left;
			margin-bottom: 10px;
			overflow: hidden;
			padding-left: 0 !important;

			img {
				width: 100%;
				height: 100%;
			}

			&:before {
				display: none;
			}
		}
	}

	.gallery_block {
		.item_gallery {
			margin: 0 0 0 0 !important
		}
	}

	.item_slide_show,
	.wrap_pic {
		margin: 0 auto 10px;
		width: auto;
		max-width: 100%;
		overflow: hidden;
	}

	.section-inner {
		position: relative;
		clear: both;
		background: #fff;
		float: left;
		width: 100%;
		overflow: hidden;

		iframe {
			width: 100%;
			max-width: 100%;
			overflow: hidden;
		}

		&.inset-column,
		&.outset-column {
			padding: 0 15px;
		}
	}

	.time_post_art {
		font: 400 0.875rem arial;
		color: #999;
		margin: 14px 0 15px 0;
	}

	h1 {
		font-size: px_to_rem(28px);
		line-height: $lh;
		color: #333;
		margin: 0 0 20px 0;
	}

	h2 {
		font-size: px_to_rem(19px);
		margin: 0 0 1.05263rem;
		line-height: 1.45;
	}

	h3 {
		margin: 0 0 1.05263rem;
		min-height: 1px;
		font-weight: 700;
		font-size: 26px;
		text-align: left;
		line-height: 1.313;
		color: #333;
	}

	ol {
		margin-bottom: 30px;
		margin-left: 20px;
		list-style-type: decimal;

		li {
			position: relative;
			padding-left: 17px;
			list-style-type: decimal;
			font: 400 19px/1.618 'IBM Plex Serif', serif;
		}
	}

	.medium-insert-embeds .video {
		position: relative;
		height: 0;
		padding-bottom: 56.25%;
	}

	.medium-insert-embeds .video iframe {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	.medium-insert-images,
	.mediumInsert {
		margin: 0 0 1em 0;
		text-align: center;
	}

	.medium-insert-embeds,
	.mediumInsert-embeds {
		margin: 0 0 1em 0;
		text-align: center;
		position: relative;
	}

	.outset-column .gallery_block {
		width: auto;
		float: none;
		margin: 0 -15px 0 -15px !important;
		overflow: hidden;

		.owl-stage {
			padding: 0 !important;
		}
	}

	.socialluu_tin span,
	.g-before-after .g-img-divider>span::before,
	.flip_wrap::before {
		font-size: 15px;
		font-family: "vnexpress-font" !important;
		font-style: normal !important;
		font-weight: normal !important;
		font-variant: normal !important;
		text-transform: none !important;
		speak: none;
		line-height: 1;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}

	.g-before-after .g-img-divider>span {
		height: 30px;
		width: 30px;
		color: #212121;
		text-indent: -9000px;
		background-color: #fff;
	}

	.g-before-after .g-img-divider>span::before {
		content: "\72";
		text-indent: 0;
		position: absolute;
		top: 0;
		left: 0;
		font-size: 31px;
	}

	.flip_wrap::before {
		content: "\22";
		position: absolute;
		font-size: 30px;
		top: 10px;
		left: 10px;
		z-index: 99;
	}

	p {
		margin: 1em 0;
		font-size: 18px;
	}

	figcaption,
	.desc_cation {
		padding: 10px 10px 0 10px;
		clear: both;
	}

	.medium-insert-images figure figcaption,
	.medium-insert-images figure .desc_cation,
	.mediumInsert figure figcaption,
	.mediumInsert figure .desc_cation,
	.medium-insert-embeds figure figcaption,
	.medium-insert-embeds figure .desc_cation,
	.mediumInsert-embeds figure figcaption,
	.mediumInsert-embeds figure .desc_cation {
		font: 400 14px arial !important;
		color: #666;
		max-width: 720px;
		margin: 0 auto;
		text-align: left;
	}

	.full-width {

		.medium-insert-images figure figcaption,
		.medium-insert-images figure .desc_cation,
		.mediumInsert figure figcaption,
		.mediumInsert figure .desc_cation,
		.medium-insert-embeds figure figcaption,
		.medium-insert-embeds figure .desc_cation,
		.mediumInsert-embeds figure figcaption,
		.mediumInsert-embeds figure .desc_cation {
			padding: 5px 15px 0;
		}
	}

	.section-inner {
		figcaption {
			text-align: left;
			font-size: px_to_rem(16px) !important;
			padding: 5px 0 0 0;
		}
	}

	.item_social {
		height: 30px;
	}

	.count_comment_total {
		.count_comment {
			margin: 1px 10px 0 0;
		}
	}

	.chapter {
		padding-bottom: 100px;
	}

	.footer {
		background: #f6f6f6;
		padding: 15px;
		position: fixed;
		bottom: 0;
		z-index: 99;
		left: 0;
		right: 0;
		box-shadow: 0px 0px 15px rgba(153, 153, 153, 0.6);
		-webkit-transform: translateY(100%);
		-ms-transform: translateY(100%);
		transform: translateY(100%);
		transition-duration: 350ms;
		transition-property: transform;
		transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
		will-change: transform;

		.nytmm_previous,
		.nytmm_next {
			width: 50%;
			margin-top: 0;

			h3 {
				font: 500 px_to_rem(16px) $fontmain;
				font-style: italic;
				margin: 0;
				display: none;

				span {
					text-transform: uppercase;
					font: 400 px_to_rem(14px) $fontmain;
					font-style: initial;
					display: block;
					margin-bottom: 2px;
				}

				a {
					color: #333;
				}

				&:before {
					content: "";
					position: absolute;
					top: 4px;
					color: #666;
					width: 20px;
					height: 44px;
				}
			}
		}

		.nytmm_previous {
			padding: 0 5px 0 20px;
		}

		.nytmm_next {
			padding: 0 20px 0 5px;
		}

		.nytmm_previous h3:before {
			background: url(images/icons/Preview.png) no-repeat;
			left: -10px;
		}

		.nytmm_next h3:before {
			background: url(images/icons/Next.png) no-repeat;
			right: -10px;
		}

		.nytmm_previous {
			float: left;
			text-align: left;
			position: relative;
			z-index: 2;
		}

		.nytmm_next {
			float: right;
			text-align: right;
			position: relative;

			h3 {
				text-align: right;
			}
		}

		&.show_sticky {
			-webkit-transform: translateY(0%);
			-ms-transform: translateY(0%);
			transform: translateY(0%);
		}

		.comment_button {
			display: none;
			position: absolute;
			left: 0;
			right: 0;
			margin: auto;

			a {
				float: right;
				margin-right: 5px;
				background: #9f224e;
				text-align: center;
				color: #fff;
				padding: 0 20px;
				text-transform: uppercase;
				display: inline-block;
				height: 44px;
				line-height: 40px;
				border: 2px solid #d6d6d6;
				border-radius: 22px;
			}
		}
	}

	#box_comment {
		padding: 0;
		margin-top: 10px;
		padding-bottom: 100px;
	}
}

/*End Detail Longform*/

