		display: flex;
		@media (max-width: 767px) {
			flex-wrap: wrap;
		}
	}
	.corona_table {
		flex-grow: 1;
		margin-right: 22px;
		font-size: 14px;
		line-height: 16px;
		max-height: 177px;
		@media (max-width: 767px) {
			width: 100%; margin-right: 0;
		}
		table {
			width: 100%;
			td {
				padding: 10px;
				border-top: 1px solid #E5E5E5;
			}
			th {
				font-weight: 400;
			}
			tr:not(:first-child){
				td:first-child {
					text-align: left;
				}
				td:not(:first-child) {
					font-weight: 700;
				}
			}
		}
	}
	.corona_virus_tab {
		margin-top: 10px;
		margin-bottom: 0;
		border-top: 1px solid #BDBDBD;
		justify-content: flex-start;
		li {
			width: auto; position: relative;
			border-bottom: none;
			color: #757575; font-weight: 400;
			padding-top: 20px;
			&.active {
				color: #222222;
				&:before {
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					margin: auto;
					content:"";
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 10px 10px 0 10px;
					border-color: #BDBDBD transparent transparent transparent;
				}
				&:after {
					position: absolute;
					top: -2px;
					left: 0;
					right: 0;
					margin: auto;
					content:"";
					width: 0;
					height: 0;
					border-style: solid;
					border-width: 10px 10px 0 10px;
					border-color: #F7F7F7 transparent transparent transparent;
				}
			}
		}
	}
	.corona_virus_data {
		@media (max-width: 767px) {
			width: 100%;
			> div {
					width: calc(50% - 5px) !important;
					margin-top: 10px !important; 
			}
		}
		> div {
			width: 100%;
		}
		.corona_nhiembenh {
			display: flex;
			flex-direction: column;
			&::before {
				content: "(dng tnh nCoV)";
				order: 3;
				font-weight: normal;
				font-size: 11px;
				line-height: 13px;
			}
		}
		.corona_tuvong {
			background: #999999;
			margin-top: 10px;
		}
	}
}